.menuBar{
    margin: 0;
    height: 50px;
    border-bottom: 5px solid #70e000;
}
.menuBar svg{
    color: #fff;
    font-size: 30px;
}
.bar{
    background-color:#282c34;
}
.barIcon{
    position: absolute;
    right: 7px;
    top: 7px;
}
.icon{
    position: absolute;
    left: 7px;
    top: 7px;
}
.btnId{
    display: none;
}
.containerMenu{
    position: absolute;
    background: rgba(0,0,0,0.5);
    width: 100%;
    height: 100vh;
    top: 0;left: 0;
    transition: all 500ms ease;
    opacity: 0;
    visibility: hidden;
}
.btnId:checked ~ .containerMenu{
    z-index: 2;
    opacity: 1;
    visibility: visible;
    position: fixed;
}
.contMenu{
    z-index: 1;
    width: 100%;
    max-width: 250px;
    background: #1c1c1c;
    height: 120%;
    position: fixed;
    transition: all 500ms ease;
    transform: translateX(100%);
}
.btnId:checked ~ .containerMenu .contMenu{
    transform: translateX(50%);
}
.contMenu nav{
    transform: translateY(15%);
}
.contMenu nav a{
    display: block;
    text-decoration: none;
    margin: 10px auto;
    padding: 20px;
    color: #c7c7c7;
    border-left: 5px solid transparent;
    transition: all 400ms ease;
}
.contMenu nav a:hover{
    border-left: 5px solid #c7c7c7;
    background: #1f1f1f;
}
.contMenu label{
    position: absolute;
    right: 15px;
    top: 10px;
    color: #fff;
    cursor: pointer;
    font-size: 18px;
}



.sidebar{
    position: fixed;
    left: 0;
    top: 0;
    height: 100%;
    width: 60px;
    background: #11101D;
    padding: 6px 14px;
    z-index: 99;
    transition: all 0.5s ease;
  }
  .sidebar.open{
    width: 250px;
  }
  .sidebar .logoDetails{
    height: 60px;
    display: flex;
    align-items: center;
    position: relative;
  }
  .sidebar .logoDetails .icoon{
    opacity: 0;
    transition: all 0.5s ease;
  }
  .sidebar .logoDetails .logoName{
    color: #fff;
    font-size: 20px;
    font-weight: 600;
    opacity: 0;
    transition: all 0.5s ease;
  }
  .sidebar.open .logoDetails .icoon,
  .sidebar.open .logoDetails .logoName{
    opacity: 1;
  }
  .sidebar .logoDetails #btn{
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
    font-size: 22px;
    transition: all 0.4s ease;
    font-size: 23px;
    text-align: center;
    cursor: pointer;
    transition: all 0.5s ease;
  }
  .sidebar.open .logoDetails #btn{
    text-align: right;
  }
  .sidebar i{
    color: #fff;
    height: 60px;
    min-width: 50px;
    font-size: 28px;
    text-align: center;
    line-height: 60px;
  }
  .sidebar .navList{
    margin-top: 20px;
    height: 100%;
    padding-left: 0;
  }
  .sidebar li{
    position: relative;
    margin: 8px 0;
    list-style: none;
  }
  .sidebar li .tooltip{
    position: absolute;
    top: -20px;
    left: calc(100% + 15px);
    z-index: 3;
    background: #fff;
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.3);
    padding: 6px 12px;
    border-radius: 4px;
    font-size: 15px;
    font-weight: 400;
    opacity: 0;
    white-space: nowrap;
    pointer-events: none;
    transition: 0s;
  }
  .sidebar li:hover .tooltip{
    opacity: 1;
    pointer-events: auto;
    transition: all 0.4s ease;
    top: 50%;
    transform: translateY(-50%);
  }
  .sidebar.open li .tooltip{
    display: none;
  }
  .sidebar input{
    font-size: 15px;
    color: #FFF;
    font-weight: 400;
    outline: none;
    height: 50px;
    width: 100%;
    width: 50px;
    border: none;
    border-radius: 12px;
    transition: all 0.5s ease;
    background: #1d1b31;
  }
  .sidebar.open input{
    padding: 0 20px 0 50px;
    width: 100%;
  }
  .sidebar .bxSearch{
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    font-size: 22px;
    background: #1d1b31;
    color: #FFF;
  }
  .sidebar.open .bxSearch:hover{
    background: #1d1b31;
    color: #FFF;
  }
  .sidebar .bxSearch:hover{
    background: #FFF;
    color: #11101d;
  }
  .sidebar li a{
    display: flex;
    height: 100%;
    width: 100%;
    border-radius: 12px;
    align-items: center;
    text-decoration: none;
    transition: all 0.4s ease;
    background: #11101D;
  }
  .sidebar li a:hover{
    background: #FFF;
  }
  .sidebar li a .linksName{
    color: #fff;
    font-size: 15px;
    font-weight: 400;
    white-space: nowrap;
    opacity: 0;
    pointer-events: none;
    transition: 0.4s;
  }
  .sidebar.open li a .linksName{
    opacity: 1;
    pointer-events: auto;
  }
  .sidebar li a:hover .linksName,
  .sidebar li a:hover i{
    transition: all 0.5s ease;
    color: #11101D;
  }
  .sidebar li i{
    height: 50px;
    line-height: 50px;
    font-size: 18px;
    border-radius: 12px;
  }
  .sidebar li.profile{
    position: fixed;
    height: 60px;
    width: 78px;
    left: 0;
    bottom: -8px;
    padding: 10px 14px;
    background: #1d1b31;
    transition: all 0.5s ease;
    overflow: hidden;
  }
  .sidebar.open li.profile{
    width: 250px;
  }
  .sidebar li .profileDetails{
    display: flex;
    align-items: center;
    flex-wrap: nowrap;
  }
  .sidebar li img{
    height: 45px;
    width: 45px;
    object-fit: cover;
    border-radius: 6px;
    margin-right: 10px;
  }
  .sidebar li.profile .name,
  .sidebar li.profile .job{
    font-size: 15px;
    font-weight: 400;
    color: #fff;
    white-space: nowrap;
  }
  .sidebar li.profile .job{
    font-size: 12px;
  }
  .sidebar .profile #log_out{
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
    background: #1d1b31;
    width: 100%;
    height: 60px;
    line-height: 60px;
    border-radius: 0px;
    transition: all 0.5s ease;
  }
  .sidebar.open .profile #log_out{
    width: 50px;
    background: none;
  }
  .homeSection{
    position: relative;
    background: #E4E9F7;
    min-height: 100vh;
    top: 0;
    left: 78px;
    width: calc(100% - 78px);
    transition: all 0.5s ease;
    z-index: 2;
  }
  .sidebar.open ~ .homeSection{
    left: 250px;
    width: calc(100% - 250px);
  }
  .homeSection .text{
    display: inline-block;
    color: #11101d;
    font-size: 25px;
    font-weight: 500;
    margin: 18px
  }
  @media only screen and (max-width: 500px) {
    .containerMenu{
        display:fixed
    }
    .contMenu{
        display: fixed
    }
    .sidebar{
        display: none
    }
 }